.Result {
    display: flex;
    flex-direction: column;
    color: white;
    animation: squeezIn 1s linear 2s;
    opacity: 0;
    animation-fill-mode: forwards;
    flex-grow: 0.00001;
    align-self: center;
}

.ResultTitle {
    font-size: 40px;
    margin-bottom: 10px;
    animation: fadeIn 1s linear 2s;
    animation-fill-mode: forwards;
    opacity: 0;
    white-space: nowrap;
}

@keyframes squeezIn {
    100% {
        opacity: 1;
        flex-grow: 1;
        width: fit-content;
    }
}

@keyframes fadeIn {
    100% {
        opacity: 1;
    }
}

@media(max-width: 650px) {
    .Result {
        order: 3;
        max-width: fit-content;
    }
}